import React, { useEffect, useState } from "react";
import { Link, useNavigate, useParams } from "react-router-dom";
import axios from "axios";
import TextField from "@mui/material/TextField";
import Button from "@mui/material/Button";
import Grid from "@mui/material/Grid";
import {
  Box,
  Card,
  Container,
  Divider,
  IconButton,
  Typography,
} from "@mui/material";
import { ArrowBack } from "@mui/icons-material";

const EditDataKegiatan = () => {
  const [nama, setNama] = useState("");
  const [jamMulai, setJamMulai] = useState("");
  const [jamSelesai, setJamSelesai] = useState("");
  const [tanggal, setTanggal] = useState("");
  const { id } = useParams();

  const navigate = useNavigate();

  useEffect(() => {
    getDataKegiatanById(id);
  }, [id]);

  const getDataKegiatanById = async (id) => {
    try {
      const response = await axios.get(
        `${process.env.REACT_APP_API}/kegiatan/${id}`
      );
      setNama(response.data.name);
      setJamMulai(response.data.jamMulai);
      setJamSelesai(response.data.jamSelesai);
      setTanggal(response.data.tanggal);
    } catch (error) {
      console.log(error.response);
    }
  };

  const saveDataKegiatan = async (e) => {
    e.preventDefault();
    try {
      await axios.patch(`${process.env.REACT_APP_API}/kegiatan/${id}`, {
        name: nama,
        jamMulai: jamMulai,
        jamSelesai: jamSelesai,
      });
      navigate("/kegiatan");
    } catch (error) {
      console.log(error.response);
    }
  };

  return (
    <div>
      <Card>
        <Divider />
        <Box display={"flex"} gap={1} alignItems={"center"}>
          <IconButton component={Link} to={"/kegiatan"}>
            <ArrowBack />
          </IconButton>
          <Typography variant="h6" color="initial">
            Edit Data Kegiatan
          </Typography>
        </Box>

        <Container sx={{ marginTop: "20px", marginBottom: "20px" }}>
          <form onSubmit={saveDataKegiatan}>
            <Grid container spacing={2}>
              <Grid item xs={12}>
                <TextField
                  id="nama"
                  label="Masukan Nama"
                  variant="outlined"
                  value={nama}
                  onChange={(e) => setNama(e.target.value)}
                  fullWidth
                  size="small"
                />
              </Grid>
              <Grid item xs={12} sm={4}>
                <TextField
                  id="tanggal"
                  label="tanggal"
                  type="date"
                  variant="outlined"
                  value={tanggal}
                  onChange={(e) => setTanggal(e.target.value)}
                  fullWidth
                  size="small"
                />
              </Grid>
              <Grid item xs={12} sm={4}>
                <TextField
                  id="jamMulai"
                  label="Waktu Mulai"
                  type="time"
                  variant="outlined"
                  value={jamMulai}
                  onChange={(e) => setJamMulai(e.target.value)}
                  fullWidth
                  size="small"
                />
              </Grid>
              <Grid item xs={12} sm={4}>
                <TextField
                  id="jamSelesai"
                  label="Waktu Selesai"
                  type="time"
                  variant="outlined"
                  value={jamSelesai}
                  onChange={(e) => setJamSelesai(e.target.value)}
                  fullWidth
                  size="small"
                />
              </Grid>
              <Grid item xs={12}>
                <Button type="submit" variant="contained" color="primary">
                  Simpan
                </Button>
                <Button to={"/kegiatan"} LinkComponent={Link}>
                  batal
                </Button>
              </Grid>
            </Grid>
          </form>
        </Container>
      </Card>
    </div>
  );
};

export default EditDataKegiatan;
