import React, { useState } from "react";
import { Link, useNavigate } from "react-router-dom";
import axios from "axios";
import TextField from "@mui/material/TextField";
import Button from "@mui/material/Button";
import Select from "@mui/material/Select";
import MenuItem from "@mui/material/MenuItem";
import {
  Container,
  Card,
  Grid,
  IconButton,
  Typography,
  Box,
} from "@mui/material";
import { ArrowBack } from "@mui/icons-material";

const InputDataPeserta = () => {
  const [name, setName] = useState("");
  const [nim, setNim] = useState("");
  const [fakultas, setFakultas] = useState("");
  const [jurusan, setJurusan] = useState("");
  const [gender, setGender] = useState("male");
  const navigate = useNavigate();

  const inputDataPeserta = async (e) => {
    e.preventDefault();
    try {
      await axios.post(`${process.env.REACT_APP_API}/peserta`, {
        name: name,
        nim: nim,
        fakultas: fakultas,
        jurusan: jurusan,
        gender: gender,
      });
      navigate("/peserta");
    } catch (error) {
      console.log(error);
      console.log("dfsdfdsfsdfsdf");
    }
  };

  return (
    <div>
      <Card>
        <Box display={"flex"} gap={1} alignItems={"center"}>
          <IconButton component={Link} to={"/peserta"}>
            <ArrowBack />
          </IconButton>
          <Typography variant="h6" color="initial">
            Add Data Kegiatan
          </Typography>
        </Box>
        <Container sx={{ marginTop: "20px", marginBottom: "20px" }}>
          <form onSubmit={inputDataPeserta}>
            <Grid container spacing={2}>
              <Grid item xs={12}>
                <TextField
                  id="name"
                  label="Nama Peserta"
                  variant="outlined"
                  value={name}
                  onChange={(e) => setName(e.target.value)}
                  fullWidth
                  size="small"
                />
              </Grid>
              <Grid item xs={12} sm={6}>
                <TextField
                  id="nim"
                  label="NIM"
                  variant="outlined"
                  value={nim}
                  onChange={(e) => setNim(e.target.value)}
                  fullWidth
                  size="small"
                />
              </Grid>
              <Grid item xs={12} sm={6}>
                <TextField
                  id="fakultas"
                  label="Fakultas"
                  variant="outlined"
                  value={fakultas}
                  onChange={(e) => setFakultas(e.target.value)}
                  fullWidth
                  size="small"
                />
              </Grid>
              <Grid item xs={12} sm={6}>
                <TextField
                  id="jurusan"
                  label="Jurusan"
                  variant="outlined"
                  value={jurusan}
                  onChange={(e) => setJurusan(e.target.value)}
                  fullWidth
                  size="small"
                />
              </Grid>
              <Grid item xs={12} sm={6}>
                <Select
                  id="gender"
                  label="Gender"
                  variant="outlined"
                  value={gender}
                  onChange={(e) => setGender(e.target.value)}
                  fullWidth
                  size="small"
                >
                  <MenuItem value="male">Male</MenuItem>
                  <MenuItem value="female">Female</MenuItem>
                </Select>
              </Grid>
              <Grid item xs={12}>
                <Button
                  type="submit"
                  variant="contained"
                  sx={{ marginRight: "10px" }}
                >
                  Input
                </Button>
                <Button to="/peserta" LinkComponent={Link}>
                  Batal
                </Button>
              </Grid>
            </Grid>
          </form>
        </Container>
      </Card>
    </div>
  );
};

export default InputDataPeserta;
