import { createSlice, createAsyncThunk } from "@reduxjs/toolkit";
import axios from "axios";

const initialState = {
  admin: null,
  isError: false,
  isSuccess: false,
  isLoading: false,
  message: "",
};

export const Login = createAsyncThunk(
  "admin/Login",
  async (admin, thunkAPI) => {
    try {
      const response = await axios.post(`${process.env.REACT_APP_API}/login`, {
        email: admin.email,
        password: admin.password,
      });
      return response.data;
    } catch (error) {
      const message = error.response.data.message;
      return thunkAPI.rejectWithValue(message);
    }
  }
);

export const getMe = createAsyncThunk("admin/getMe", async (_, thunkAPI) => {
  try {
    const response = await axios.get(`${process.env.REACT_APP_API}/me`);
    return response.data;
  } catch (error) {
    const message = error.response.data.message;
    return thunkAPI.rejectWithValue(message);
  }
});

export const LogOut = createAsyncThunk("admin/LogOut", async () => {
  await axios.delete(`${process.env.REACT_APP_API}/logout`);
});

export const authSlice = createSlice({
  name: "atuh",
  initialState,
  reducers: {
    reset: (state) => initialState,
  },
  extraReducers: (builder) => {
    builder.addCase(Login.pending, (state) => {
      state.isLoading = true;
    });
    builder.addCase(Login.fulfilled, (state, action) => {
      state.isLoading = false;
      state.isSuccess = true;
      state.admin = action.payload;
    });
    builder.addCase(Login.rejected, (state, action) => {
      state.isLoading = false;
      state.isError = true;
      state.message = action.payload;
    });

    // Get admin Login
    builder.addCase(getMe.pending, (state) => {
      state.isLoading = true;
    });
    builder.addCase(getMe.fulfilled, (state, action) => {
      state.isLoading = false;
      state.isSuccess = true;
      state.admin = action.payload;
    });
    builder.addCase(getMe.rejected, (state, action) => {
      state.isLoading = false;
      state.isError = true;
      state.message = action.payload;
    });
  },
});

export const { reset } = authSlice.actions;
export default authSlice.reducer;
