import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";

import {
  Table,
  TableHead,
  TableBody,
  TableRow,
  TableCell,
  Button,
  Paper,
  TableContainer,
  Typography,
  IconButton,
  Box,
} from "@mui/material";
import Delete from "@mui/icons-material/Delete";
import EditIcon from "@mui/icons-material/Edit";
import VisibilityIcon from "@mui/icons-material/Visibility";
import axios from "axios";
import { LinearProgress } from "@mui/material";

const ListDataKegiatan = () => {
  const [dataKegiatan, setDataKegiatan] = useState([]);
  const [loading, setLoading] = useState(false);

  useEffect(() => {
    getAllDataKegiatan();
  }, []);

  const getAllDataKegiatan = async () => {
    setLoading(true);
    try {
      const response = await axios.get(`${process.env.REACT_APP_API}/kegiatan`);
      console.log(response.data);
      setLoading(false);
      setDataKegiatan(response.data);
    } catch (error) {
      setLoading(false);
      console.log(error);
    }
  };

  const deleteDataKegiatan = async (key) => {
    try {
      await axios.delete(`${process.env.REACT_APP_API}/kegiatan/${key}`);
      getAllDataKegiatan();
    } catch (error) {
      console.log(error);
    }
  };

  const updateStatus = async (id) => {
    try {
      await axios.patch(`${process.env.REACT_APP_API}/kegiatan/status/${id}`);
      getAllDataKegiatan();
    } catch (error) {
      console.log(error);
    }
  };

  return (
    <div>
      <TableContainer
        component={Paper}
        sx={{
          paddingX: "20px",
          boxSizing: "border-box",
        }}
      >
        <Box
          display="flex"
          sx={{ margin: "10px" }}
          justifyContent={"space-between"}
        >
          <Typography variant="h5" color="initial">
            Data Kegiatan
          </Typography>
          <Button variant="contained" LinkComponent={Link} to={"/kegiatan/add"}>
            add data
          </Button>
        </Box>
        {loading && <LinearProgress />}
        <Table size="small">
          <TableHead>
            <TableRow>
              <TableCell sx={{ fontWeight: "bold" }}>no</TableCell>
              <TableCell sx={{ fontWeight: "bold" }}>Nama</TableCell>
              <TableCell sx={{ fontWeight: "bold" }}>Tanggal</TableCell>
              <TableCell sx={{ fontWeight: "bold" }} align="center">
                Jam
              </TableCell>
              <TableCell sx={{ fontWeight: "bold" }} align="center">
                Key Kegiatan
              </TableCell>
              <TableCell sx={{ fontWeight: "bold" }} align="center">
                Status
              </TableCell>
              <TableCell sx={{ fontWeight: "bold" }} align="center">
                Action
              </TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {dataKegiatan.map((data, index) => (
              <TableRow key={data.uuid}>
                <TableCell sx={{ fontWeight: "bold" }}>{index + 1}</TableCell>
                <TableCell> {data.name} </TableCell>
                <TableCell>{data.tanggal}</TableCell>
                <TableCell align="center">
                  {data.jamMulai} - {data.jamSelesai}
                </TableCell>
                <TableCell align="center">{data.kegiatanKey}</TableCell>
                <TableCell align="center">
                  <IconButton
                    variant="text"
                    size="small"
                    color={data.status ? "primary" : ""}
                    onClick={() => {
                      updateStatus(data.uuid);
                    }}
                  >
                    {data.status ? "Aktif" : "Non-Aktif"}
                  </IconButton>
                </TableCell>
                <TableCell align="center">
                  <IconButton
                    size="small"
                    LinkComponent={Link}
                    to={`/kegiatan/edit/${data.uuid}`}
                    color="primary"
                  >
                    <EditIcon />
                  </IconButton>
                  <IconButton
                    size="small"
                    LinkComponent={Link}
                    to={`/kegiatan/detail/${data.kegiatanKey}`}
                    color="primary"
                  >
                    <VisibilityIcon />
                  </IconButton>
                  <IconButton
                    size="small"
                    color="error"
                    onClick={() => {
                      deleteDataKegiatan(data.kegiatanKey);
                    }}
                  >
                    <Delete />
                  </IconButton>
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>
    </div>
  );
};

export default ListDataKegiatan;
