import React, { useEffect, useState } from "react";
import { useParams, useNavigate, Link } from "react-router-dom";
import {
  Container,
  TextField,
  Button,
  Grid,
  Card,
  MenuItem,
  Box,
  Typography,
  IconButton,
} from "@mui/material";
import { ArrowBack } from "@mui/icons-material";
import axios from "axios";

const EditDataPeserta = () => {
  const [name, setName] = useState("");
  const [nim, setNim] = useState("");
  const [fakultas, setFakultas] = useState("");
  const [jurusan, setJurusan] = useState("");
  const [gender, setGender] = useState("male");
  const { id } = useParams();
  const navigate = useNavigate();

  useEffect(() => {
    getDataPesertaById(id);
  }, [id]);

  const getDataPesertaById = async (id) => {
    try {
      const response = await axios.get(
        `${process.env.REACT_APP_API}/peserta/${id}`
      );
      setName(response.data.name);
      setNim(response.data.nim);
      setFakultas(response.data.fakultas);
      setJurusan(response.data.jurusan);
      setGender(response.data.gender);
    } catch (error) {
      console.log(error.response);
    }
  };

  const saveDataPeserta = async (e) => {
    e.preventDefault();
    try {
      await axios.patch(`${process.env.REACT_APP_API}/peserta/${id}`, {
        name: name,
        nim: nim,
        fakultas: fakultas,
        jurusan: jurusan,
        gender: gender,
      });
      navigate("/peserta");
    } catch (error) {
      console.log(error.response);
    }
  };

  return (
    <div>
      <Card>
        <Box display={"flex"} gap={1} alignItems={"center"}>
          <IconButton component={Link} to={"/peserta"}>
            <ArrowBack />
          </IconButton>
          <Typography variant="h6" color="initial">
            Edit Data Peserta
          </Typography>
        </Box>
        <Container sx={{ marginTop: "20px", marginBottom: "20px" }}>
          <form onSubmit={saveDataPeserta}>
            <Grid container spacing={2}>
              <Grid item xs={12}>
                <TextField
                  id="name"
                  label="Nama Peserta"
                  variant="outlined"
                  value={name}
                  onChange={(e) => setName(e.target.value)}
                  fullWidth
                  size="small"
                />
              </Grid>
              <Grid item xs={12} sm={6}>
                <TextField
                  id="nim"
                  label="NIM"
                  variant="outlined"
                  value={nim}
                  onChange={(e) => setNim(e.target.value)}
                  fullWidth
                  size="small"
                />
              </Grid>
              <Grid item xs={12} sm={6}>
                <TextField
                  id="fakultas"
                  label="Fakultas"
                  variant="outlined"
                  value={fakultas}
                  onChange={(e) => setFakultas(e.target.value)}
                  fullWidth
                  size="small"
                />
              </Grid>
              <Grid item xs={12} sm={6}>
                <TextField
                  id="jurusan"
                  label="Jurusan"
                  variant="outlined"
                  value={jurusan}
                  onChange={(e) => setJurusan(e.target.value)}
                  fullWidth
                  size="small"
                />
              </Grid>
              <Grid item xs={12} sm={6}>
                <TextField
                  id="gender"
                  label="Gender"
                  variant="outlined"
                  value={gender}
                  onChange={(e) => setGender(e.target.value)}
                  fullWidth
                  select
                  size="small"
                >
                  <MenuItem value="male">Male</MenuItem>
                  <MenuItem value="female">Female</MenuItem>
                </TextField>
              </Grid>
              <Grid item xs={12}>
                <Button type="submit" variant="contained" color="primary">
                  Simpan
                </Button>
                <Button to="/peserta" LinkComponent={Link}>
                  Batal
                </Button>
              </Grid>
            </Grid>
          </form>
        </Container>
      </Card>
    </div>
  );
};

export default EditDataPeserta;
