import React, { useState } from "react";
import axios from "axios";
import TextField from "@mui/material/TextField";
import {
  Button,
  Card,
  TableBody,
  TableHead,
  Typography,
  TableContainer,
} from "@mui/material";
import Grid from "@mui/material/Grid";
import SearchIcon from "@mui/icons-material/Search";
import Table from "@mui/material/Table";
import TableCell from "@mui/material/TableCell";
import TableRow from "@mui/material/TableRow";
import Dialog from "@mui/material/Dialog";
import DialogActions from "@mui/material/DialogActions";
import DialogContent from "@mui/material/DialogContent";
import DialogContentText from "@mui/material/DialogContentText";
import Alert from "@mui/material/Alert";
import CheckIcon from "@mui/icons-material/Check";
import { IconButton } from "@mui/material";
import BorderColorIcon from "@mui/icons-material/BorderColor";
import { LinearProgress } from "@mui/material";

const FormPresensi = () => {
  const [lihatData, setLihatData] = useState(false);
  const [error, setError] = useState("");
  const [open, setOpen] = useState(false);
  const [dataKegiatan, setDataKegiatan] = useState([]);
  const [dataPesrta, setDataPesrta] = useState();
  const [nim, setNim] = useState("");
  const [success, setSuccess] = useState(false);
  const [keyKegiatan, setKeyKegiatan] = useState("");
  const [loading, setLoading] = useState(false);

  const fetchAbsentData = async (nim) => {
    setLoading(true);
    try {
      // setLihatData(true);
      const responseAbsent = await axios.get(
        `${process.env.REACT_APP_API}/absent/peserta/${nim}`
      );
      setDataKegiatan(responseAbsent.data.keigatanData);
      setDataPesrta(responseAbsent.data.peserta);
      setLihatData(true);
      setLoading(false);
      console.log(responseAbsent.data, "absent");
    } catch (error) {
      setLoading(false);
      setLihatData(false);
      setError(error.response.data.message);
    }
  };

  const absent = async () => {
    try {
      await axios.post(`${process.env.REACT_APP_API}/absent`, {
        id: dataPesrta.uuid,
        key: keyKegiatan,
      });
      setSuccess(true);
      setTimeout(() => {
        setOpen(false);
        setSuccess(false);
        fetchAbsentData(nim);
      }, 2000);
    } catch (error) {
      setError(error.response.data.error);
    }
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    fetchAbsentData(nim);
  };

  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  const handleChange = (e) => {
    const inputValue = e.target.value;
    const maxLength = 4;
    if (inputValue.length <= maxLength) {
      setKeyKegiatan(inputValue);
    } else {
      setKeyKegiatan(inputValue.slice(0, maxLength));
    }
  };

  let dataKegiatanFilter;
  if (dataKegiatan) {
    dataKegiatanFilter = dataKegiatan.filter(
      (kegiatan) => kegiatan.status === 1
    );
  }

  return (
    <Card sx={{ paddingX: "10px", paddingTop: "10" }}>
      {loading && <LinearProgress />}
      <Typography textAlign={"center"} variant="h5" color="primary">
        PRESENTSI
      </Typography>
      <form onSubmit={handleSubmit}>
        <Grid container marginY={1} rowSpacing={1} columnSpacing={1}>
          <Grid item xs={12} sm={6}>
            <TextField
              id="Masukan"
              label="Masukan ID"
              type="search"
              size="small"
              value={nim}
              onChange={(e) => setNim(e.target.value)}
              fullWidth
              maxRows={4}
            />
          </Grid>
          <Grid item xs={12} sm={6}>
            <Button variant="contained" endIcon={<SearchIcon />} type="submit">
              Cari
            </Button>
          </Grid>
        </Grid>
      </form>
      <div>
        {lihatData ? (
          <div>
            <Table size="small">
              <TableBody>
                <TableRow>
                  <TableCell sx={{ fontWeight: "bold" }}>Nama</TableCell>
                  <TableCell>{dataPesrta.name}</TableCell>
                </TableRow>
                <TableRow>
                  <TableCell sx={{ fontWeight: "bold" }}>ID</TableCell>
                  <TableCell>{dataPesrta.nim}</TableCell>
                </TableRow>
                <TableRow>
                  <TableCell sx={{ fontWeight: "bold" }}>Fakultas</TableCell>
                  <TableCell>{dataPesrta.fakultas}</TableCell>
                </TableRow>
                <TableRow>
                  <TableCell sx={{ fontWeight: "bold" }}>Jurusan</TableCell>
                  <TableCell>{dataPesrta.jurusan}</TableCell>
                </TableRow>
              </TableBody>
            </Table>

            <div>
              <Typography textAlign={"center"} marginTop={1} variant="h6">
                Kegiatan
              </Typography>

              <TableContainer>
                <Table size="small">
                  <TableHead>
                    <TableRow>
                      <TableCell sx={{ fontWeight: "bold" }}>Nama</TableCell>
                      <TableCell sx={{ fontWeight: "bold" }} align="center">
                        Tanggal
                      </TableCell>
                      <TableCell sx={{ fontWeight: "bold" }} align="center">
                        Jam
                      </TableCell>
                      {/* <TableCell sx={{ fontWeight: "bold" }} align="center">
                        Status
                      </TableCell> */}
                      <TableCell sx={{ fontWeight: "bold" }} align="center">
                        Presensi
                      </TableCell>
                    </TableRow>
                  </TableHead>
                  {dataKegiatanFilter &&
                    dataKegiatanFilter.map((data) => (
                      <TableBody key={data.uuid}>
                        <TableRow>
                          <TableCell>{data.name}</TableCell>
                          <TableCell align="center">{data.tanggal}</TableCell>
                          <TableCell align="center">
                            {data.jamMulai} - {data.jamSelesai}
                          </TableCell>
                          <TableCell align="center">
                            {data.statusAbsen === "Sudah Absen" ? (
                              <CheckIcon color="success" />
                            ) : (
                              <div>
                                <IconButton
                                  size="small"
                                  name={"absen"}
                                  onClick={handleClickOpen}
                                >
                                  <BorderColorIcon color="primary" />
                                </IconButton>
                                <Dialog open={open} onClose={handleClose}>
                                  <div>
                                    {success ? (
                                      <Alert severity="success">Success</Alert>
                                    ) : (
                                      <div>
                                        <DialogContent>
                                          <DialogContentText>
                                            Masukkan Key Kegiatan 4 angka
                                          </DialogContentText>
                                          {error && (
                                            <Alert severity="warning">
                                              {error}
                                            </Alert>
                                          )}
                                          <TextField
                                            autoFocus
                                            margin="dense"
                                            id="name"
                                            label="Contoh: '1234'"
                                            type="number"
                                            fullWidth
                                            variant="standard"
                                            value={keyKegiatan}
                                            onChange={handleChange}
                                            maxLength={4}
                                          />
                                        </DialogContent>
                                        <DialogActions>
                                          <Button onClick={handleClose}>
                                            Batal
                                          </Button>
                                          <Button onClick={absent}>
                                            Absent
                                          </Button>
                                        </DialogActions>
                                      </div>
                                    )}
                                  </div>
                                </Dialog>
                              </div>
                            )}
                          </TableCell>
                        </TableRow>
                      </TableBody>
                    ))}
                </Table>
              </TableContainer>
            </div>
          </div>
        ) : (
          error && <Alert severity="warning">{error}</Alert>
        )}
      </div>
    </Card>
  );
};

export default FormPresensi;
