import React, { useState } from "react";
import { NavLink, useNavigate } from "react-router-dom";
import {
  AppBar,
  Container,
  Toolbar,
  Typography,
  Box,
  Drawer,
  List,
  MenuItem,
} from "@mui/material";
import { Logout, Login, Menu } from "@mui/icons-material";
import { useDispatch, useSelector } from "react-redux";
import { LogOut, reset } from "../../features/authSlice";
import logo from "../../assets/logounipa.png";
import IconButton from "@mui/material/IconButton";

const Navbar = () => {
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const { admin } = useSelector((state) => state.auth);
  const [openDrawer, setOpenDrawer] = useState(false);

  const logout = () => {
    dispatch(LogOut());
    dispatch(reset());
    navigate("/login");
  };

  const toggleDrawer = () => {
    setOpenDrawer(!openDrawer);
  };

  const ComponenTolbar = (props) => {
    return (
      <>
        {admin ? (
          <Box sx={{ display: "flex", flexDirection: props.a }} gap={1}>
            <MenuItem
              component={NavLink}
              to="/peserta"
              color="inherit"
              activeclassname="active"
              sx={{
                "&.active": {
                  backgroundColor: "#90CAF9",
                },
              }}
            >
              Peserta
            </MenuItem>

            <MenuItem
              component={NavLink}
              to="/kegiatan"
              color="inherit"
              activeclassname="active"
              sx={{
                "&.active": {
                  backgroundColor: "#90CAF9",
                },
              }}
            >
              Kegiatan
            </MenuItem>
            <MenuItem onClick={logout}>
              LogOut
              <Logout />
            </MenuItem>
          </Box>
        ) : (
          <MenuItem component={NavLink} color="inherit" to="/login">
            LogIn
            <Login />
          </MenuItem>
        )}
      </>
    );
  };

  return (
    <AppBar
      // component={AppBar}
      position="sticky"
      color="primary"
      // boxShadow={"none"}
    >
      <Container maxWidth="lg">
        <Toolbar>
          <Box
            component="div"
            sx={{ flexGrow: 1 }}
            display={"flex"}
            alignItems={"center"}
            gap={2}
          >
            <img src={logo} alt="log unipa" width={50} height={"auto"} />
            <Typography variant="h6">Presensi UNIPA</Typography>
          </Box>

          <Box display={{ xs: "none", sm: "flex" }} gap={1}>
            <ComponenTolbar />
          </Box>
          <Box display={{ xs: "flex", sm: "none" }} gap={1}>
            <IconButton color="inherit" onClick={toggleDrawer}>
              <Menu />
            </IconButton>
          </Box>
        </Toolbar>

        <Drawer anchor="right" open={openDrawer} onClose={toggleDrawer}>
          <Box
            sx={{
              width: "240px",
              padding: "16px",
            }}
          >
            <List>
              <ComponenTolbar a={"column"} />
            </List>
          </Box>
        </Drawer>
      </Container>
    </AppBar>
  );
};

export default Navbar;
