import React, { useState } from "react";
import { Link, useNavigate } from "react-router-dom";
import axios from "axios";
import TextField from "@mui/material/TextField";
import Button from "@mui/material/Button";
import Grid from "@mui/material/Grid";
import { Box, Card, Container, IconButton, Typography } from "@mui/material";
import { ArrowBack } from "@mui/icons-material";

const InputDataKegiatan = () => {
  const [name, setName] = useState("");
  const [jamMulai, setJamMulai] = useState("");
  const [jamSelesai, setJamSelesai] = useState("");
  const [date, setDate] = useState("");
  const navigate = useNavigate();

  const inputDataKegiatan = async (e) => {
    e.preventDefault();

    try {
      await axios.post(`${process.env.REACT_APP_API}/kegiatan`, {
        name: name,
        jamMulai: jamMulai,
        jamSelesai: jamSelesai,
        tanggal: date,
      });
      navigate("/kegiatan");
    } catch (error) {
      console.log(error);
    }
  };

  return (
    <div>
      <Card>
        <Box display={"flex"} gap={1} alignItems={"center"}>
          <IconButton component={Link} to={"/kegiatan"}>
            <ArrowBack />
          </IconButton>
          <Typography variant="h6" color="initial">
            Add Data Kegiatan
          </Typography>
        </Box>
        <Container sx={{ marginTop: "20px", marginBottom: "20px" }}>
          <form onSubmit={inputDataKegiatan}>
            <Grid container spacing={2}>
              <Grid item xs={12}>
                <TextField
                  id="nama"
                  label="Masukan Nama"
                  variant="outlined"
                  value={name}
                  onChange={(e) => setName(e.target.value)}
                  fullWidth
                  size="small"
                />
              </Grid>
              <Grid item xs={12} sm={4}>
                <TextField
                  id="date"
                  // label="Waktu Mulai"
                  type="date"
                  variant="outlined"
                  value={date}
                  onChange={(e) => setDate(e.target.value)}
                  fullWidth
                  size="small"
                />
              </Grid>
              <Grid item xs={12} sm={4}>
                <TextField
                  id="jamMulai"
                  label="Waktu Mulai"
                  type="time"
                  variant="outlined"
                  value={jamMulai}
                  onChange={(e) => setJamMulai(e.target.value)}
                  fullWidth
                  size="small"
                />
              </Grid>
              <Grid item xs={12} sm={4}>
                <TextField
                  id="jamSelesai"
                  label="Waktu Selesai"
                  type="time"
                  variant="outlined"
                  value={jamSelesai}
                  onChange={(e) => setJamSelesai(e.target.value)}
                  fullWidth
                  size="small"
                />
              </Grid>
              <Grid item xs={12}>
                <Button type="submit" variant="contained" color="primary">
                  Simpan
                </Button>
                <Button to={"/kegiatan"} LinkComponent={Link}>
                  Batal
                </Button>
              </Grid>
            </Grid>
          </form>
        </Container>
      </Card>
    </div>
  );
};

export default InputDataKegiatan;
