import React from "react";
import { BrowserRouter, Routes, Route } from "react-router-dom";
import Presensi from "./pages/Presensi";
import DataPeserta from "./pages/DataPeserta";
import DataKegiatan from "./pages/DataKegiatan";
import InputPeserta from "./pages/InputPeserta";
import EditPeserta from "./pages/EditPeserta";
import InputKegiatan from "./pages/InptuKegiatan";
import EditKegitan from "./pages/EditKegiatan";
import DetailKegiatan from "./pages/DetailKegiatan";
import FomrLogin from "./components/login/FormLogin";
function App() {
  return (
    <div>
      <BrowserRouter>
        <Routes>
          <Route path="/" element={<Presensi />} />
          <Route path="/login" element={<FomrLogin />} />
          <Route path="/peserta" element={<DataPeserta />} />
          <Route path="/peserta/add" element={<InputPeserta />} />
          <Route path="/peserta/edit/:id" element={<EditPeserta />} />
          <Route path="/kegiatan" element={<DataKegiatan />} />
          <Route path="/kegiatan/add" element={<InputKegiatan />} />
          <Route path="/kegiatan/edit/:id" element={<EditKegitan />} />
          <Route path="/kegiatan/detail/:key" element={<DetailKegiatan />} />
        </Routes>
      </BrowserRouter>
    </div>
  );
}

export default App;
