import React, { useEffect, useState } from "react";
import { Link, useParams } from "react-router-dom";
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableRow,
  Paper,
  Typography,
  TableContainer,
  Box,
  IconButton,
  Divider,
} from "@mui/material";
import axios from "axios";
import Delete from "@mui/icons-material/Delete";
import { ArrowBack } from "@mui/icons-material";

const DetailDataKegitan = () => {
  const [dataAbsent, setDataAbsent] = useState([]);
  const [dataKegiatan, setDataKegiatan] = useState([]);
  const { key } = useParams();

  useEffect(() => {
    getDataAbsent(key);
  }, [key]);

  const getDataAbsent = async (key) => {
    try {
      const response = await axios.get(
        `${process.env.REACT_APP_API}/absent/kegiatan/${key}`
      );
      setDataAbsent(response.data.pesertaData);
      setDataKegiatan(response.data.kegiatan);
    } catch (error) {
      console.log(error.response.data.message);
    }
  };

  const deleteStatus = async (id, key) => {
    try {
      await axios.delete(
        `${process.env.REACT_APP_API}/absent/kegiatan/${id}/${key}`
      );
      getDataAbsent(key);
    } catch (error) {
      console.log(error.response);
    }
  };

  return (
    <div>
      <TableContainer component={Paper}>
        <Box display={"flex"} gap={1} alignItems={"center"}>
          <IconButton component={Link} to={"/kegiatan"}>
            <ArrowBack />
          </IconButton>
          <Typography variant="h6" color="initial">
            Detail Data Kegiatan
          </Typography>
        </Box>
        <Box
          sx={{
            paddingX: "20px",
            boxSizing: "border-box",
          }}
        >
          <Divider />
          <Table size="small" sx={{ maxWidth: "sm" }}>
            <TableBody>
              <TableRow>
                <TableCell sx={{ fontWeight: "bold" }}>Nama Kegiatan</TableCell>
                <TableCell>{dataKegiatan.name}</TableCell>
              </TableRow>
              <TableRow>
                <TableCell sx={{ fontWeight: "bold" }}>Tanggal</TableCell>
                <TableCell>{dataKegiatan.tanggal}</TableCell>
              </TableRow>
              <TableRow>
                <TableCell sx={{ fontWeight: "bold" }}>Jam</TableCell>
                <TableCell>
                  {dataKegiatan.jamMulai} - {dataKegiatan.jamSelesai}{" "}
                </TableCell>
              </TableRow>
              <TableRow>
                <TableCell sx={{ fontWeight: "bold" }}>Key Kegiatan</TableCell>
                <TableCell>{dataKegiatan.kegiatanKey}</TableCell>
              </TableRow>
              <TableRow>
                <TableCell sx={{ fontWeight: "bold" }}>Status</TableCell>
                <TableCell>
                  {dataKegiatan.status ? "Aktif" : "Non-Aktif"}
                </TableCell>
              </TableRow>
            </TableBody>
          </Table>

          <Table size="small">
            <TableHead>
              <Typography variant="h6" marginTop={1} color="initial">
                Peserta
              </Typography>
              <TableRow>
                <TableCell sx={{ fontWeight: "bold" }}>No</TableCell>
                <TableCell sx={{ fontWeight: "bold" }}>Nama</TableCell>
                <TableCell sx={{ fontWeight: "bold" }}>ID</TableCell>
                <TableCell sx={{ fontWeight: "bold" }}>Fakultas</TableCell>
                <TableCell sx={{ fontWeight: "bold" }}>Jurusan</TableCell>
                <TableCell sx={{ fontWeight: "bold" }}>Gender</TableCell>
                <TableCell sx={{ fontWeight: "bold" }}>Status</TableCell>
                <TableCell sx={{ fontWeight: "bold" }} align="center">
                  Hapus Status
                </TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {dataAbsent.map((data, index) => (
                <TableRow key={data.uuid}>
                  <TableCell sx={{ fontWeight: "bold" }}>{index + 1}</TableCell>
                  <TableCell>{data.name}</TableCell>
                  <TableCell>{data.nim}</TableCell>
                  <TableCell>{data.fakultas}</TableCell>
                  <TableCell>{data.jurusan}</TableCell>
                  <TableCell>{data.gender}</TableCell>
                  <TableCell>{data.statusAbsen}</TableCell>
                  <TableCell align="center">
                    <IconButton
                      size="small"
                      variant="text"
                      color="error"
                      onClick={() => deleteStatus(data.uuid, key)}
                    >
                      <Delete />
                    </IconButton>
                  </TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </Box>
      </TableContainer>
    </div>
  );
};

export default DetailDataKegitan;
