import React, { useState, useEffect } from "react";
import axios from "axios";
import { Link } from "react-router-dom";
import {
  Table,
  TableHead,
  TableBody,
  TableRow,
  TableCell,
  Button,
  Paper,
  TableContainer,
  IconButton,
  Typography,
  Box,
} from "@mui/material";
import Delete from "@mui/icons-material/Delete";
import EditIcon from "@mui/icons-material/Edit";
import { LinearProgress } from "@mui/material";

const ListDataPeserta = () => {
  const [dataPeserta, setDataPeserta] = useState([]);
  const [loading, setLoading] = useState(false);

  useEffect(() => {
    getAllDataPeserta();
  }, []);

  const getAllDataPeserta = async () => {
    setLoading(true);
    try {
      const response = await axios.get(`${process.env.REACT_APP_API}/peserta`);
      console.log(response.data);
      setLoading(false);
      setDataPeserta(response.data);
    } catch (error) {
      setLoading(false);
      console.log(error);
    }
  };
  const deleteDataKegiatan = async (id) => {
    try {
      await axios.delete(`${process.env.REACT_APP_API}/peserta/${id}`);
      getAllDataPeserta();
    } catch (error) {
      console.log(error);
    }
  };
  return (
    <div>
      <TableContainer
        component={Paper}
        sx={{
          paddingX: "20px",
          boxSizing: "border-box",
        }}
      >
        <Box
          display="flex"
          sx={{ margin: "10px" }}
          justifyContent={"space-between"}
        >
          <Typography variant="h5" color="initial">
            Data Peserta
          </Typography>
          <Button variant="contained" LinkComponent={Link} to={"/peserta/add"}>
            add data
          </Button>
        </Box>
        {loading && <LinearProgress />}
        <Table size="small">
          <TableHead>
            <TableRow>
              <TableCell sx={{ fontWeight: "bold" }}>No</TableCell>
              <TableCell sx={{ fontWeight: "bold" }}>Nama</TableCell>
              <TableCell sx={{ fontWeight: "bold" }}>ID</TableCell>
              <TableCell sx={{ fontWeight: "bold" }}>Fakultas</TableCell>
              <TableCell sx={{ fontWeight: "bold" }}>Jurusan</TableCell>
              <TableCell sx={{ fontWeight: "bold" }}>Gender</TableCell>
              <TableCell sx={{ fontWeight: "bold" }} align="center">
                Action
              </TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {dataPeserta.map((data, index) => (
              <TableRow key={data.uuid}>
                <TableCell sx={{ fontWeight: "bold" }}>{index + 1}</TableCell>
                <TableCell> {data.name}</TableCell>
                <TableCell>{data.nim}</TableCell>
                <TableCell>{data.fakultas}</TableCell>
                <TableCell>{data.jurusan}</TableCell>
                <TableCell>{data.gender}</TableCell>
                <TableCell align="center">
                  <IconButton
                    size="small"
                    LinkComponent={Link}
                    color="primary"
                    to={`/peserta/edit/${data.uuid}`}
                  >
                    <EditIcon />
                  </IconButton>

                  <IconButton
                    size="small"
                    color="error"
                    onClick={() => {
                      deleteDataKegiatan(data.uuid);
                    }}
                  >
                    <Delete />
                  </IconButton>
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>
    </div>
  );
};

export default ListDataPeserta;
